<template>
  <div>
    <vs-card class="p-5">
      <a href="javascript:void()" @click="$router.go(-1)">‚Üê Back</a>
      <h4 class="inline-block ml-2">{{church.name}}</h4>
      <div class="contained-example-container vs-con-loading__container" id="div-with-loading">
        <div class="vx-row">
          <div class="vx-col w-full lg:w-1/4 xl:w-1/4">
            <div class="churchimg">
              <div class="img-container mt-6 pt-4 text-center">
                <img v-if="imgpreview" :src="previewUrl" class="logowidth" />
                <div v-else>
                  <img v-if="churchimg" :src="churchimg" class="logowidth" />
                </div>
                <div class="mt-5">
                  <div class v-if="imgpreview">
                    <vs-button
                      @click="saveImageUpload()"
                      color="warning"
                      type="filled"
                      class="mr-2 mb-2"
                    >Save Image</vs-button>
                    <vs-button @click="imgpreview = false" color="danger" type="flat">Cancel</vs-button>
                  </div>
                  <div v-else>
                    <input id="fileUpload" @change="handleFileChange($event)" type="file" hidden />
                    <vs-button
                      @click="changeImage()"
                      color="dark"
                      type="filled"
                      class="mr-2 mb-2"
                    >Change Image</vs-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="vx-col w-full lg:w-3/4 xl:w-3/4">
            <form class="mt-4">
              <div class="vx-row">
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Church Name"
                    v-model="church.name"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <label>Country</label>
                  <v-select
                    @input="changeCountry"
                    class="mt-1"
                    v-model="church.countryId"
                    label="name"
                    :options="countries"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <label>State</label>
                  <v-select class="mt-1" v-model="church.state" label="name" :options="states" />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Region"
                    v-model="church.region"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Country Code"
                    v-model="church.countryCode"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>

                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Phone Number"
                    v-model="church.phoneNumber"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Email"
                    v-model="church.email"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Address"
                    v-model="church.address"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Zone"
                    v-model="church.zone"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>
                <div class="vx-col w-1/2 pt-3">
                  <vs-input
                    label-placeholder="Pastor"
                    v-model="church.pastor"
                    name="input1"
                    class="w-full mt-5"
                  />
                </div>

                <!-- <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Transaction Cost"
                v-model="church.transCost"
                name="input1"
                class="w-full mt-5"
              />
                </div>-->

                <!-- <div class="vx-col w-1/2 pt-3">
              <vs-input
                label="Logo"
                accept="image/png, image/jpeg, image/jpg"
                @change="handleFileChange($event)"
                name="input1"
                type="file"
                class="w-full mt-0"
              />
                </div>-->
              </div>
              <vs-button type="filled" @click.prevent="submitForm" class="mt-5 block">Edit Account</vs-button>
            </form>
          </div>
        </div>
      </div>
    </vs-card>
  </div>
</template>

<script>
import vSelect from "vue-select";
export default {
  components: {
    "v-select": vSelect
  },
  data() {
    return {
      countries: [],
      states: [],
      churchInfo: {},
      imgpreview: false,
      churchimg: "",
      imageDataUpload: "",
      previewUrl: "",
      church: {
        name: "",
        state: "",
        region: "",
        countryCode: "",
        phoneNumber: "",
        email: "",
        countryId: "",
        address: "",
        zone: "",
        pastor: ""
      }
    };
  },
  mounted() {
    this.openLoadingInDiv();
    this.getChurchDetails();
    this.$store
      .dispatch("getCountries")
      .then(resp => {
        this.countries = resp.data;
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      })
      .catch(err => {
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      });
  },
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    handleFileChange: function(event) {
      //   console.log(event.target.files[0]);
      const file = event.target.files[0];
      this.imageDataUpload = file;
      this.previewUrl = URL.createObjectURL(file);
      this.imgpreview = true;
      //   this.church.Logo = event.target.files[0];
    },
    saveImageUpload() {
      this.openLoadingInDiv();
      let churchData = {
        logo: this.imageDataUpload,
        churchId: this.$route.query._id
      };
      this.$store
        .dispatch("updateChurchLogo", churchData)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.$vs.notify({
            title: "Church Logo",
            text: "Church Logo updated successfully!",
            color: "success"
          });
          this.getChurchDetails();
          this.imgpreview = false;
          //   this.churchInfo = resp.data;
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          //   console.log(err.response);
          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Logo",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Logo",
                text: "Unable to edit church logo, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to edit church logo, try again!",
              color: "warning"
            });
          }
        });
    },
    getChurchDetails() {
      this.openLoadingInDiv();
      const churchId = this.$route.params.id;
      this.$store
        .dispatch("getBranchDetails", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.churchInfo = resp.data;
          this.church.name = this.churchInfo.name;
          this.church.countryId = this.churchInfo.countryId;
          this.church.state = this.churchInfo.state;
          this.church.region = this.churchInfo.region;
          this.church.countryCode = this.churchInfo.countryCode;
          this.church.phoneNumber = this.churchInfo.phoneNumber;
          this.church.email = this.churchInfo.email;
          this.church.address = this.churchInfo.address;
          this.church.zone = this.churchInfo.zone;
          this.church.pastor = this.churchInfo.pastor;
          this.churchimg =
            "https://d1fp61h5e226jt.cloudfront.net/" + this.churchInfo.img;
          //    this.church.transCost = this.churchInfo.pastor;
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          console.log(err.response);

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Account",
                text: "Unable to load church details, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to load church details, try again!",
              color: "warning"
            });
          }
        });
    },
    changeCountry(value) {
      this.openLoadingInDiv();
      let countryId = value._id;
      this.$store
        .dispatch("getState", countryId)
        .then(resp => {
          this.states = resp.data;
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
        });
    },
    changeImage() {
      document.getElementById("fileUpload").click();
      //   console.log(event.target.files[0]);
      //   this.church.Logo = event.target.files[0];
    },
    submitForm() {
      // this.church.state = this.church.state.name;
      // this.church.countryId = this.church.countryId._id;
      this.openLoadingInDiv();

      let editChurchData = {
        church: this.church,
        churchId: this.$route.query._id
      };
      this.$store
        .dispatch("editChurch", editChurchData)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.$vs.notify({
            title: "Edit Church Account",
            text: "Church Account editted successfully!",
            color: "success"
          });
          //   console.log(resp);
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Create Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Edit Church Account",
                text: "Unable to edit Church account, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Edit Church Account",
              text: "Unable to edit Church account, try again!",
              color: "warning"
            });
          }
        });
    }
  }
};
</script>
